                <el-form-item>
                    <el-input v-if="batchType === '面积(元/m²)'" v-model.trim="batchForm.inputVal"></el-input>
                    <el-input v-if="batchType === '铝锭价'" v-model.trim="batchForm.inputVal"></el-input>
                    <el-input v-if="batchType === '表面加工'" v-model.trim="batchForm.inputVal"></el-input>
                    <el-input v-if="batchType === '后工序'" v-model.trim="batchForm.inputVal"></el-input>
                    <el-input v-if="batchType === '特殊材质费'" v-model.trim="batchForm.inputVal"></el-input>
                    <el-input v-if="batchType === '特殊包装费'" v-model.trim="batchForm.inputVal"></el-input>
                    <el-input v-if="batchType === '长料费'" v-model.trim="batchForm.inputVal"></el-input>
                    <el-input v-if="batchType === '小料费'" v-model.trim="batchForm.inputVal"></el-input>
                    <el-input v-if="batchType === '机型费'" v-model.trim="batchForm.inputVal"></el-input>
                    <el-input v-if="batchType === '锯挂痕费'" v-model.trim="batchForm.inputVal"></el-input>
                    <el-input v-if="batchType === '撕筋费'" v-model.trim="batchForm.inputVal"></el-input>
                    <el-input v-if="batchType === '禁喷加工费'" v-model.trim="batchForm.inputVal"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="batchVisible = false">取 消</el-button>
                <el-button class="btn-cyan add-btn" @click="">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import CommonMixins from '$src/components/CommonMixins'
    import CommonSearch from '$src/components/CommonSearch'
    import CommonTable from '$src/components/CommonTable'
    import CommonPagination from '$src/components/CommonPagination'
    import CommonDetailX from '$src/components/CommonDetailX'
    export default {
        mixins: [CommonMixins],
        components: {
            CommonSearch,
            CommonTable,
            CommonPagination,
            CommonDetailX
        },
        data() {
            return {
                mainHeight: 716,
                isResize: true,
                isShowStatus: {
                    isMore: false,
                    isRead: true,
                    isPopUp: false
                },
                returnStatusName: {
                    '0':'未提交',
                    '50':'已提交',
                    '100':'已审核',
                    'null':'未提交'
                },
                isShowChildMatCode: true,
                currentIndex: 0,
                clickTimes: 0,
                editIndex: 0,
                tableHeightI: this.$store.state.screenHei*0.62,
                tableHeightII: 150,
                otherVariable: {
                    isSpread: false,
                    contentWidth: 70,
                    popupTitle: '',
                    //isMulSelect: true,
                    isInlineForm: true,
                    isEdit: false,
                    isChildEdit: false,
                    isHeadComplex: true,
                    isFormComplex: true,
                    loading: false,
                    $isCustom: true
                },
                showTable: false,
                tempWidth: null,
                tableData: [],
                showPrice: false,
                showUnPrice: false,
                //搜索映射表
                searchProp: [
                    {
                        name:'装车单',
                        prop:'refDocCode',
                        type:'date',
                        isMainSearch:true,
                        isMain:false,
                        placeholder: '请输入装车单号'
                    },
                    {
                        name:'装车价格状态',
                        prop:'tempStatus',
                        type:'selectChange',
                        selectList:[{value:0,label:'全部'},{value:1,label:'未提交'},{value:2,label:'已提交'},{value:3,label:'已审核'}],
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'下单日期',
                        prop:'orderDocDateStart',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'至',
                        prop:'orderDocDateEnd',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'销售公司',
                        prop:'companyName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'供货公司',
                        prop:'supplyCompanyName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                ],
                //详情映射列表
                formProp: [
                    {name:'装车单号',prop:'refDocCode',type:'outter'},
                    {name:'装车日期',prop:'docDate',type:'outter'},
                    {name:'交货日期',prop:'deliveryDate',type:'outter'},
                    {name:'单据状态',prop:'priceStatus',type:'outter',isStatus:true},
                    {name:'已审价',prop:'isCheckPrice',type:'outter',isCheck:true},
                    {name:'客户',prop:'clientCode',type:'inner'},
                    {name:'经销商',prop:'clientCode2',type:'inner'},
                    {name:'合同号',prop:'contractCode',type:'inner'},
                    {name:'铝锭取价方式',prop:'alPriceMethodName',type:'inner'},
                    {name:'铝锭价',prop:'alPrice',type:'inner'},
                    {name:'销售公司',prop:'companyName',type:'inner'},
                    {name:'取价日期',prop:'alPriceDate',type:'inner'},
                    {name:'结算方式',prop:'settlementMethodName',type:'inner'},
                    {name:'铝价日期',prop:'alPriceDate',type:'inner'},
                    {name:'备注',prop:'remark',type:'inner',isLong:true}
                ],
                //表格映射列表
                tableProp: [
                    {
                        name:'fid',
                        prop:'fid',
                        type:'normal',
                        isMain:true
                    },
                    {
                        name1:'装车单号/',
                        prop1:'refDocCode',
                        name2:'状态',
                        prop2:'priceStatus',
                        type:'is_check_price',
                        width: '130'
                    },
                    {
                        name1:'流程单号/',
                        prop1:'docCode',
                        name2:'装车日期',
                        prop2:'docDate',
                        type:'mixin',
                        width: '130'
                    },
                    {
                        name1:'客户/',
                        prop1:'clientCode',
                        name2:'经销商',
                        prop2:'clientCode2',
                        type:'mixin',
                        width: '100'
                    },
                    {
                        name1:'送货地区/',
                        prop1:'destination',
                        name2:'提货方式',
                        prop2:'loaderMethodName',
                        type:'mixin',
                    }
                ],
                searchForm: {
                    minPriceStatus: 0,
                    maxPriceStatus: 50
                },
                detailForm: {},
                detailId: null,
                contractCode: null,
                docCode: null,
                pageObj: {
                    commonPage: 1,
                    commonSize: 10,
                    commonSizes: [10, 20, 30, 40, 50, 100],
                    commonTotal: 0
                },
                unitList: [],
                //详情表格内容
                tabLeft: '100px',
                activeName: 'first',
                //订单明细数据
                tabDataI: [],
                copyDataI: [],
                loadingI: false,
                isShowNotAllowed: false,
                refMatPrice: null,
                refMatProcessPrice: null,
                tabPropIII: [
                    {prop:'itemNo', name:'序号',},
                    {prop:'priceItemText', name:'合同条款内容',type:'normal',width:'100'},
                    {
                        prop:'', name:'价格',
                        children:[
                            {prop:'otherPrice',name:'面积(元/m²)',type:'normal'},
                            {prop:'price',name:'重量(元/kg)',type:'normal'},

                        ]
                    },
                    {
                        prop:'', name:'价格组成',
                        children:[
                            {prop:'',name:'加工费'},
                            {prop:'',name:'运算费'},
                            {prop:'',name:'打托费'},
                            {prop:'',name:'附加费'},

                        ]
                    },
                    {
                        prop:'', name:'特批不收费',
                        children:[
                            {prop:'',name:'运输'},
                            {prop:'',name:'打托'},

                        ]
                    },
                ],
                tabDataIII:[],
                //批量修改
                batchTitle: '',
                batchVisible: false,
                batchType: '',
                batchForm: {},
                //修改合同
                contractList: [],
                contractVisible: false,
                priceContract: ''
            }
        },
        watch: {
            'otherVariable.isChildEdit':{
                handler(val) {
                    this.isShowNotAllowed = val;
                }
            }
        },
        mounted() {
            this.getData();
            this.getUnitList();
            // 操作/布局相关
            this.listenResizeIII();
            this.changeSizeIII();
            this.tempWidth = this.otherVariable.contentWidth;
            //表格禁止右键弹出默认浏览器菜单
            this.$refs.pricedetail.$el.oncontextmenu = function() {return false};
        },
        methods: {
            //获取主表数据
            getData(isFirst = true) {
                this.tableData = [];
                this.fetch(`/oms/sls/LoaderDoc/getLoaderAndContractInfo?pageNo=${this.pageObj.commonPage}&pageSize=${this.pageObj.commonSize}`,this.searchForm)
                    .then(result => {
                        if(result.data && result.data.list) {
                            this.tableData = result.data.list;
                            this.pageObj.commonTotal = result.data.total;
                            if(isFirst) {
                                this.$nextTick(() => {
                                    this.$refs.commontable.renderRow(this.tableData[0].fid);
                                })
                            }else {
                                this.$nextTick(() => {
                                    this.$refs.commontable.renderRow(this.detailId);
                                })
                            }
                        }else {
                            this.pageObj.commonTotal = 0;
                        }
                    });
            },
            selectChange(val) {
                if(val === 0) {
                    this.searchForm.minPriceStatus = 0;
                    this.searchForm.maxPriceStatus = 101;
                }else if(val === 1) {
                    this.searchForm.minPriceStatus = 0;
                    this.searchForm.maxPriceStatus = 50;
                }else if(val === 2) {
                    this.searchForm.minPriceStatus = 50;
                    this.searchForm.maxPriceStatus = 100;
                }else if(val === 3) {
                    this.searchForm.minPriceStatus = 100;
                    this.searchForm.maxPriceStatus = 100;
                }
            },
            getSearchList(){
                this.pageObj.commonPage=1;
                this.pageObj.commonSize=10;
                this.getData();
            },
            handleReset(){
                this.searchForm = {minPriceStatus: 0,maxPriceStatus: 50};
                this.handleRefresh();
            },
            //刷新
            handleRefresh() {
                this.getData();
                this.otherVariable.isChildEdit = false;
                this.tabDataI = [];
                this.showPrice = false;
                this.showUnPrice = false;
                this.detailForm = {};
            },
            //点击主表行
            handleDetail(row) {
                this.isShowNotAllowed = true;
                this.loadingI = true;
                if(row.priceStatus === 0 || row.priceStatus === null) {
                    this.showPrice = true;
                    this.showUnPrice = false;
                }else if(row.priceStatus === 50){
                    this.showPrice = false;
                    this.showUnPrice = true;
                }else {
                    this.showPrice = false;
                    this.showUnPrice = false;
                }
                this.isShowStatus.isRead = true;
                this.detailForm = row;
                this.showTable = false;
                this.refMatPrice = row.alPrice;
                this.tabDataIII = [];
                this.contractCode = row.contractCode;
                this.detailId = row.fid;
                this.priceContract = row.contractCode;
                this.docCode = row.docCode;
                this.getDetailTable(row.fid)
            },
            //获取从表
            getDetailTable(fid) {
                this.tabDataI = [];
                this.copyDataI = [];
                this.priceIdsArray = [];
                this.otherVariable.isChildEdit = false;
                this.isShowNotAllowed = true;
                this.getAjax(`/oms/sls/SalesLoaderPriceController/getLoaderPrice?loaderFlowId=${fid}&contractCode=${this.contractCode}`)
                    .then(result => {
                        this.loadingI = false;
                        this.isShowNotAllowed = false;
                        this.isShowNotAllowed = false;
                        if(result.status === 200 && result.data) {
                            this.tabDataI = result.data;
                            this.copyDataI = this.$lodash.cloneDeep(this.tabDataI);
                            result.data.forEach(item => {
                                item.alPrice = this.refMatPrice;
                                this.priceIdsArray.push(item.priceId)
                            });
                        }else {
                            this.tabDataI = [];
                            this.copyDataI = [];
                        }
                    })
                    .then(() => {
                        this.loadingI = false;
                    })
            },
            //提交
            handlePrice() {
                this.$confirm('是否提交?','提示',{
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(()=>{
                    let resultStatus;
                    this.fetch('/oms/sls/SalesLoaderPriceController/submitPrice', {loaderFlowId:this.detailId,priceStatus:50,priceIdsArray:this.$lodash.compact(this.priceIdsArray)})
                        .then(result => {
                            resultStatus = result.status;
                            if(result.status === 200) {
                                this.$message({
                                    message:'操作成功',
                                    type:'success'
                                });
                                this.searchForm.tempStatus === 0 ? this.getData(false) : this.getData();
                            }else {
                                this.$message({
                                    message:this.returnErrorMsg(result.error),
                                    type:'error'
                                })
                            }
                        })
                })

            },
            //撤销
            handldUnPrice() {
                this.$confirm('是否撤销?','提示',{
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(()=>{
                    let resultStatus;
                    this.fetch('/oms/sls/SalesLoaderPriceController/submitPrice', {loaderFlowId:this.detailId,priceStatus:0,priceIdsArray:this.$lodash.compact(this.priceIdsArray)})
                        .then(result => {
                            resultStatus = result.status;
                            if(result.status === 200) {
                                this.$message({
                                    message:'操作成功',
                                    type:'success'
                                });
                                this.searchForm.tempStatus === 0 ? this.getData(false) : this.getData();
                            }else {
                                this.$message({
                                    message:this.returnErrorMsg(result.error),
                                    type:'error'
                                })
                            }
                        })
                })

            },
            //从表点击行
            clickDetail(row) {
                if(this.detailForm.isCheckPrice !== 1) {
                    this.showTable = false;
                    if(row.isLocked !== 1 && !this.otherVariable.isReadTable && this.showPrice) {
                        this.otherVariable.isChildEdit = true;
                        this.tabDataI = this.giveState(this.tabDataI);
                        row.isEditState = true;
                    }
                    this.childDetailRow = row || {};
                    this.clickTimes += 1;
                    if(this.clickTimes === 1) {
                        this.refMatProcessPrice = this.$lodash.cloneDeep(this.childDetailRow).pricePart1;
                    }
                    this.editIndex = this.$lodash.findIndex(this.tabDataI, ['priceId', row.priceId]);
                    if(this.editIndex < 0) {
                        this.editIndex = 0;
                    }
                }
            },
            //从表保存
            handleSaveChild() {
                let tempObj;
                let api = '/oms/sls/SalesLoaderPriceController/updatePcfMatPrice';
                let model = this.childDetailRow;
                model.sjgPrice = 0.000;
                model.jpPrice = 0.000;
                model.contractId = null;
                model.contractCode = null;
                model.alPriceDate = null;
                model.fid = this.childDetailRow.priceId;
                model.isLocked = this.childDetailRow.isLocked ? 1 : 0;
                model.price = Number(this.childDetailRow.price);
                model.refMatPrice = Number(this.childDetailRow.pricePart1);
                model.refMatProcessPrice = Number(this.childDetailRow.pricePart2);
                model.otherPrice = Number(this.childDetailRow.otherPrice);
                model.pricePart1 = Number(this.childDetailRow.pricePart1);
                model.pricePart2 = Number(this.childDetailRow.pricePart2);
                model.pricePart3 = Number(this.childDetailRow.pricePart3);
                model.pricePart4 = Number(this.childDetailRow.pricePart4);
                model.usrPricePart1 = Number(this.childDetailRow.usrPricePart1);
                model.usrPricePart2 = Number(this.childDetailRow.usrPricePart2);
                model.usrPricePart3 = Number(this.childDetailRow.usrPricePart3);
                model.usrPricePart4 = Number(this.childDetailRow.usrPricePart4);
                model.usrPricePart5 = Number(this.childDetailRow.usrPricePart5);
                model.usrPricePart6 = Number(this.childDetailRow.usrPricePart6);
                model.usrPricePart7 = Number(this.childDetailRow.usrPricePart7);
                model.usrPricePart8 = Number(this.childDetailRow.usrPricePart8);
                model.usrPricePart9 = Number(this.childDetailRow.usrPricePart9);
                model.usrPricePart10 = Number(this.childDetailRow.usrPricePart10);
                tempObj = {
                    loaderFlowId: this.detailForm.fid,
                    priceOperateModel: model
                };
                this.fetch(api,tempObj)
                    .then(result => {
                        if(result.status === 200) {
                            this.$message({
                                message: '保存成功',
                                type: 'success'
                            });
                            this.otherVariable.isChildEdit = false;
                            this.getDetailTable(this.detailId);
                            this.clickTimes = 0;
                        }else {
                            this.$message({
                                message: this.returnErrorMsg(result.error),
                                type: 'error'
                            });
                        }
                    });
                this.tabDataI = this.giveState(this.tabDataI)
            },
            //从表锁价
            handleLock() {
                this.getAjax(`/oms/sls/SalesOrderPriceController/doLockPrice?priceId=${this.childDetailRow.priceId}`)
                    .then(result => {
                        if(result.status === 200) {
                            this.$message({
                                message: '锁价成功',
                                type: 'success'
                            });
                            this.otherVariable.isChildEdit = false;
                            this.getDetailTable(this.detailId);
                        }else {
                            this.$message({
                                message: '锁价失败',
                                type: 'error'
                            });
                        }
                    })
            },
            toggleChildMatcode() {
                this.isShowChildMatCode = !this.isShowChildMatCode;
                if(!this.isShowChildMatCode) {
                    this.tabDataI = this.$lodash.cloneDeep(this.copyDataI)
                }else {
                    this.tabDataI = this.tabDataI.filter(item => !item.parentVariantId)
                }
            },
            //从表取消操作
            handleCancelChild() {
                this.isShowChildMatCode = false;
                this.tabDataI = this.$lodash.cloneDeep(this.copyDataI);
                this.otherVariable.isChildEdit = false;
                this.clickTimes = 0;
            },
            //禁止点击表格提醒
            handleNotAllowed() {
                if(this.otherVariable.isChildEdit) {
                    this.$confirm('此操作将清空已填写的数据, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.handleCancelChild();
                    }).catch(() => {
                        console.log('cnacel')
                    })
                }
            },
            isSaveChildRow() {
                this.$confirm('是否保存?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.handleSaveChild();
                }).catch(() => {
                    console.log('cancel')
                    //this.handleCancelChild();
                })
            },
            //批量修改
            showBatchDialog(column, event) {
                if(!this.otherVariable.isChildEdit) {
                    this.batchType = '';
                    const arr = ['面积(元/m²)','铝锭价','表面加工','后工序','特殊材质费','特殊包装费','长料费','小料费','机型费','锯挂痕费','撕筋费','禁喷加工费'];
                    if(arr.includes(column.label) && this.showPrice) {
                        this.batchType = column.label;
                        this.batchVisible = true;
                        this.batchForm = {};
                        if(this.batchType === '面积(元/m²)') {
                            this.batchTitle = '批量修改面积单价'
                        }else if(this.batchType === '表面加工' || this.batchType === '后工序') {
                            this.batchTitle = `批量修改${this.batchType}价格`
                        }else {
                            this.batchTitle = `批量修改${this.batchType}`
                        }
                    }
                }
            },
            handleBatchEdit() {
                //this.detailId
            },
            //获取价格明细详情
            handleOtherTable(row) {
                if(this.otherVariable.isChildEdit) {
                    this.$message({
                        message: '编辑状态中',
                        type: 'warning'
                    })
                }else {
                    this.showTable = !this.showTable;
                    if(this.showTable) {
                        this.isResize = false;
                        this.tableHeightI = this.tableHeightI - 150;
                        this.showTable = true;
                        this.$nextTick(() => {
                            this.isResize = true;
                        })
                    }else {
                        this.tableHeightI = this.tableHeightI + 150;
                    }
                    this.tabDataIII = [];
                    //请求数据
                    this.fetch('/oms/sls/SalesOrderPriceController/getMatPriceItem', {matPriceId:row.priceId})
                        .then(result => {
                            if (result.status === 200) {
                                this.tabDataIII = result.data.list;
                            }else {
                                this.tabDataIII = [];
                            }
                        })
                        .then(() => {
                            if(this.showTable) {
                                this.$refs['tabTableII'].initStatus();
                            }
                        })
                }
            },
            //获取单位列表
            getUnitList() {
                this.getAjax('/basedata/sys/DataDictionary/getDataDictionary?docCode=30126')
                    .then(result => {
                        if(result.data) {
                            result.data.forEach(item => {
                                item.dictValue = Number(item.dictValue)
                            });
                            this.unitList = this.formatArray(result.data, 'dictText', 'dictValue');
                        }
                    })
            },
            // 获取合同列表
            getContractList(val) {
                this.contractList = [];
                if(val) {
                    this.fetch('/oms/pqm/Contract/getContractMaster?pageSize=1000',{docCode:val})
                        .then(result => {
                            if(result.data && result.data.list) {
                                this.contractList = result.data.list;
                            }else {
                                this.contractList = [];
                            }
                        })
                }else {
                    this.contractList = [];
                }
            },
            // Edit By RayLua
            // 保存合同
            handleSaveContract() {
                if(this.detailId && this.priceContract) {
                    let resultStatus;
                    this.fetch(`/oms/sls/LoaderDoc/append-contract`, {fId:this.detailId, contractCode: this.priceContract})
                        .then(result => {
                            resultStatus = result.status;
                            if(result.status === 200) {
                                this.$message({
                                    message: '保存合同成功',
                                    type: 'success'
                                });
                                this.contractVisible = false;
                                this.getData(false);
                            }else {
                                let msg = (result && result.message) || (result && result.data && result.data.message);
                                this.$message({
                                    message: msg || '保存合同失败',
                                    type: 'error'
                                });
                            }
                        })
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    .popup-enter-active, .popup-leave-active {
        transition: left 0.1s;
        left: 0;
    }
    .popup-enter, .popup-leave-to {
        left: 100%;
    }
    .menu-icon-finance {
        background-size: 27px !important;
        background-position: center;
    }
    .btn-position-I {
        position: absolute;
        bottom: 2px;
        right: 40px!important;
    }
    .btn-position-II {
        position: absolute;
        bottom: 2px;
        right: 100px!important;
    }
    .btn-position-III {
        position: absolute;
        bottom: 2px;
        right: 160px!important;
    }
</style>



